import {ToggleButton, ToggleButtonConfig} from './togglebutton';
import {UIInstanceManager} from '../uimanager';

/**
 * A button that toggles the embed video popup.
 */
export class EmbedVideoToggleButton extends ToggleButton<ToggleButtonConfig> {

  constructor(config: ToggleButtonConfig = {}) {
    super(config);

    this.config = this.mergeConfig(config, {
      cssClass: 'ui-embedvideotogglebutton',
      text: 'Embed Video'
    }, this.config);
  }

  configure(player: bitmovin.player.Player, uimanager: UIInstanceManager): void {
    super.configure(player, uimanager);

    let popup = {
      visible : false,
      isVisible: () => {
        return popup.visible;
      },
      hidePopup: () => {
        popup.visible = false;
        console.log('Hide Embed Video Popup')
      },
      showPopup: () => {
        popup.visible = true;
        console.log('Show Embed Video Popup')
      }
    }

    this.onClick.subscribe(() => {
      if (popup.isVisible()) {
        popup.hidePopup();
      } else {
        popup.showPopup();
      }
    });
  }
}